.login-button1{
	// @extend .col-sm-12;
	width: 100%;
	// height:50px;
	height: 58px;
	border:1px solid #F58331;
	border-radius: 12px;
	float:left;
	text-align:center;
	cursor:pointer; 
	position:relative;
	box-sizing:border-box;
	overflow:hidden;
	margin:20px auto;
	
	background:#F58331;
	button{
	  color: #F58331;
  }
  }
  .login-button1 button{

	font-family:$sc-font-family;
	font-size:22.8px;
	color:#fff;
	text-decoration:none;
	// line-height:50px;
	line-height: 58px;
	transition:all .4s ease;
	z-index:2;
	position:relative;
	display: inline-block;
    width: 100%;
  }
  .login-eff-1{
	// width: 220px;
	width: 100%;
	height:58px;
	// left: -220px;
	left: -100%;
	background:#fff;
	position:absolute;
	transition:all .4s ease;
	z-index:1;
  }
  .login-button1:hover .login-eff-1{
	left:0;
  }
  .login-button1 .anchor-1{
	color:#fff;
	background: transparent;
}
  .login-button1:hover button{
	color:#F58331;
  }
  @media (max-width: 450px) {
	// .about-us-video-cms-area{
	//   width: 100%;
	// }
	// .about-us-video-text-cms-area{
	//   width: auto;
	// }
	.login-button1{
		width: 100%;
		box-sizing: border-box;
		float: none;
		// display: none;
	}
	.login-eff-1{
		width: 100%;
		left: -100%;
	}
}
